.wrapper {
  font-size: 2.5rem;
  font-family: 'Nunito', sans-serif;
  font-family: 'Chakra Petch', sans-serif;
  font-family: 'Quantico', sans-serif;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 2px 2px 5px black;
}
.me {
  font-size: 2.5rem;
  margin-top: -20px;
}
.me span {
  font-size: 5rem;
  font-family: 'Square Peg';
}
.greeting {
  font-size: 3.2rem;
}
@media only screen and (max-width: 768px) {
  .wrapper {
    font-size: 2rem;
  }
  .greeting {
    font-size: 2.5rem;
  }
  .me {
    font-size: 2rem;
  }
  .me span {
    font-size: 4rem;
  }
}
