@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700&family=Poppins:ital,wght@0,300;0,400;0,500;1,600&family=Quantico:ital,wght@0,700;1,400&family=Square+Peg&display=swap');
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
  color: #e1e2e2;
  overflow-x: hidden;
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lora', serif;

  background: #5daa68 url('./images/retrowave-bg.gif') no-repeat fixed center;
  background-size: cover;
  background-blend-mode: multiply;
}
a {
  text-decoration: none;
  color: inherit;
}
.nav-toggle {
  position: absolute;
  font-size: 2.2rem;
  top: 10px;
  right: 10px;
  z-index: 2;
  transition: 0.2s linear;
}
.project {
  opacity: 0.2;
  transition: 0.3s linear;
}
.project.active {
  opacity: 1;
  transform: scale(1.1, 1.1);
}

@media screen and (max-width: 480px) {
  .nav-toggle {
    font-size: 2.2rem;
  }
}
.navigation {
  position: fixed;
  right: -100%;
  font-size: 3.5rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: #0000004d url('./images/nav-bg.jpg') no-repeat fixed center;
  background-size: cover;
  background-blend-mode: multiply;
  height: 100vh;
  z-index: 1;
  font-family: 'Quantico';
  text-shadow: 1px 1px 5px black;
  transition: 0.7s ease;
}
@media only screen and (max-width: 768px) {
  .navigation {
    font-size: 3rem;
  }
}
.navigation.active {
  right: 0;
}
.navigation a {
  width: 70%;
  border-radius: 5px;
  transition: 0.2s linear;
}
.navigation a:hover {
  transform: scale(1.1, 1.1);
  cursor: pointer;
  background-color: darkslategray;
}
.toggler {
  z-index: 2;
}

.nav-toggle:hover {
  cursor: pointer;
  color: #3f6844;
}
