@media only screen and (max-width: 768px) {
  .about {
    width: 80%;
    font-size: 1.2rem;
    gap: 10px;
    text-align: left;
  }
  .about h1 {
    font-size: 2rem;
  }
  .skills {
    gap: 10px;
    flex-direction: row;
    bottom: 10px;
    left: unset;
  }
  .lastlet::after {
    width: 25px;
    left: 15px;
    bottom: 10px;
  }
  .html,
  .css,
  .react,
  .bash,
  .linux,
  .js {
    font-size: 1.5rem;
  }
}
